<script>
  export default {
    name: 'Contact',
    props:['jsonData'],
    data: () => ({
      copiedEmail: false,
    }),
    methods: {
      copyText(text) {
        navigator.clipboard.writeText(text);
        this.copiedEmail = true;
      },
      callPhone(text) {
        window.open('tel:' + text);
      },
      openLink(url){
        window.open(url);
      }
    },
  }
</script>