<template>
  <v-app id="app">
    <div>
      <v-toolbar
      style="margin:10px;"
      class="glassMorph"
    >
    <v-toolbar-title v-if="this.$vuetify.breakpoint.width<600" style="font-size:15px">
      <b>{{jsonData.firstName}} {{jsonData.lastName}}</b>
    </v-toolbar-title>
    <v-toolbar-title v-else>
      <b>{{jsonData.firstName}} {{jsonData.lastName}}</b>
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <div style="max-width:80px;">
      <v-select
      outlined
      v-model="select"
      :items="languages"
      item-text="code"
      color="white"
      item-color="white"
      class="mt-7"
    >
      <template slot="selection" slot-scope="slotProps">
        <country-flag :country='slotProps.item.flag' size='normal'/>
      </template>
        <template v-slot:item="slotProps" >
          <v-row
          class="d-inline" style="text-align:center;">
            <country-flag :country='slotProps.item.flag' size='normal'/>
            <span class="ml-2">{{slotProps.item.name}}</span>
          </v-row>
        </template>
      </v-select>
    </div>
    <v-btn
        text
        @click="scroll('contactForm')"
      >
        <span v-if="this.$vuetify.breakpoint.width>=600" class="mr-5 ml-2">{{this.jsonData.contact.contactMessageButton}}</span>
        <v-icon>mdi-card-account-mail</v-icon>
      </v-btn>
    </v-toolbar>
    </div>

    <v-main class="mx-3">
      <Header v-bind:jsonData="jsonData" class="mb-5"/>
      <Experience v-bind:jsonData="jsonData" class="mb-5"/>
      <Education v-bind:jsonData="jsonData" class="mb-5"/>
      <Skill v-bind:jsonData="jsonData" class="mb-5"/>
      <Project v-bind:jsonData="jsonData" class="mb-5"/>
      <Language v-bind:jsonData="jsonData" class="mb-5"/>
      <Hobby v-bind:jsonData="jsonData" class="mb-5"/>
      <Contact v-bind:jsonData="jsonData" class="mb-5" id="contactForm" />
    </v-main>
    <Footer v-bind:jsonData="jsonData"/>
  </v-app>
</template>

<script>
import Header from './components/Header';
import jsonDataFile from '../jsonData.json';
import Experience from './components/Experience.vue';
import Footer from './components/Footer.vue';
import Education from './components/Education.vue';
import Skill from './components/Skill.vue';
import Project from './components/Project.vue';
import Language from './components/Language.vue';
import Hobby from './components/Hobby.vue';
import Contact from './components/Contact.vue';

export default {
  name: 'App',
  components: {
    Header,
    Experience,
    Footer,
    Education,
    Skill,
    Project,
    Language,
    Hobby,
    Contact
  },
  data: () => ({
      jsonData: jsonDataFile.french,
      languages: jsonDataFile.languages.countries,
      select: jsonDataFile.languages.countries[0]
  }),
  watch: {
    select: function(newVal) {
      if(newVal=="Fr"){
       this.jsonData = jsonDataFile.french;
      }
      else if(newVal==="En"){
       this.jsonData = jsonDataFile.english;
      }
  },
  },
  methods: {
    scroll(id) {  
      document.getElementById(id).scrollIntoView({
        behavior: "smooth"
      })
  },
  },
  mounted(){
    let lang = navigator.language || navigator.userLanguage;
    if (/^en\b/.test(lang)) {
      this.jsonData = jsonDataFile.english;
      this.select = jsonDataFile.languages.countries[1];
    }
    else {
      this.jsonData = jsonDataFile.french;
      this.select = jsonDataFile.languages.countries[0];
    }
  }
}
</script>
<style scoped>
#app {
  background-image: linear-gradient( 109.6deg,  rgba(39,142,255,1) 11.2%, rgba(98,113,255,0.78) 100.2% );;
}
.glassMorph {
  border-radius: 15px !important;
  background: rgba( 0, 0, 0, 0.35 ) !important;
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 )!important; 
  backdrop-filter: blur( 5px ) !important;
  -webkit-backdrop-filter: blur( 5px ) !important;
  border-radius: 10px !important;
  border: 1px solid rgba( 255, 255, 255, 0.18 ) !important;
}

v-input__slot {
    align-items: stretch;
    min-height: 10px;
}
</style>
